<template>
    <ul class="mui-table-view">
        <li class="mui-table-view-cell"
            v-for="todo in todos"
            style="list-style-type:none;">
            <router-link :to="{name:'todo',params:{id:todo.id}}" style="text-decoration:none;" class="">
                <div class="cont">
                    <h3>
                        <span style="color: #2d2d2d; padding:10px 0;font-weight: 400;font-size: 18px;">{{todo.title}}</span>
                    </h3>
                    <div class="info">
                        <span class="author">
                            <span class="tag" style="font-weight: 200;">青岛新海岸新区</span>
                            <span class="gap_point">•</span>
                            <span style="font-weight: 200;" class="name">{{todo.author_id}}</span>
                            <span class="gap_point">•</span>
                            <span style="color: #575757;font-weight: 200;">{{todo.updated_at}}</span>
                        </span>
                    </div>
                </div>
            </router-link>
        </li>
    </ul>
</template>

<script>
    export default {
        name: 'todos',
        props: ['todos'],
    }
</script>



